.select {
    position: absolute;
    top: calc(50% - 12px);
    left: calc(50% - 95px);
    cursor: pointer;
    display: flex;
    align-items: center;
    border: none;
    width: 100%;
    max-width: $select-width;
    height: $select-height;
    border-radius: 5px;
    background-color: #fff;
    padding: 0 15px;
    z-index: 100;
    &--open {
        .select {
            &__dropdown {
                display: block; }
            &__backdrop {
                display: block; } } }
    &__input {
        &::after {
            content: '';
            position: absolute;
            background-image: url('../images/header/dropdown-icon.svg');
            top: calc(50% - 6.5px);
            right: 15px;
            width: 12px;
            height: 11px; } }
    &__dropdown {
        display: none;
        position: absolute;
        box-shadow: 0px 0px 15px -5px #543b97;
        background-color: $color-white;
        border-radius: 5px;
        width: 100%;
        max-width: $select-width;
        max-height: $select-max-height;
        top: 0;
        left: 0;
        right: 0;
        padding: 0 15px;
        text-align: center; }
    &__list {
        margin: 0;
        padding: 0; }
    &__backdrop {
        position: fixed;
        display: none;
        z-index: -1;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        background: transparent; }
    &__item {
        padding: 8px 15px;
        transition: color .3s ease-out;
        &.selected {
            color: $color-hover;
            font-weight: 700; }
        &:hover {
            color: $color-hover;
            cursor: pointer; } } }
