.header {
    &-top {
        background-color: #eeeeee;
        position: relative;
        &__wrapp {
            @include flex-sb(); }
        &__links {
            @include flex-sb();
            flex-basis: 248px; }
        &__link {
            position: relative;
            font-size: 14px;
            line-height: 39px;
            margin-left: 28px;
            color: $color-black;
            transition: color .3s ease-out;
            &:hover {
                color: $color-hover; }
            &:first-child::before,
            &:last-child::before {
                content: '';
                position: absolute;
                width: 20px;
                height: 15.79px;
                top: calc(50% - 8px);
                left: -28px; }
            &:first-child::before {
                background: url('../images/header/home-icon.svg'); }
            &:last-child::before {
                background: url('../images/header/business-icon.svg'); }
            &:first-child:hover::before {
                background: url('../images/header/hover/home-icon-hover.svg'); }
            &:last-child:hover::before {
                background: url('../images/header/hover/business-icon-hover.svg'); } } } }

@import './modules/select';

.phone {
    &__link {
        color: $color-select-phone-link;
        font-size: 16px;
        line-height: 40px;
        font-weight: 700; } }

.header {
    &-bottom {
        &__wrapp {
            @include flex-sb();
            padding: 13.5px 0; }
        &__btn {
            margin-right: -30px; } } }

.nav {
    flex-basis: 570px;
    &__list {
        @include flex-sb(); }
    &__item {
        cursor: pointer; }
    &__link {
        color: $color-black;
        font-size: 14px;
        line-height: 58px;
        transition: color .3s ease-out;
        &:hover {
            color: $color-hover; } } }

.btn {
    @include flex-c();
    flex-basis: 170px;
    border-radius: 5px;
    background-color: $cokor-btn;
    &__link {
        margin-left: 8px;
        font-size: 14px;
        line-height: 43px;
        color: $color-white; } }
