.footer {
    background-color: #eee;
    color: $color-footer-text;
    margin-top: auto;
    &__wrapp {
        display: flex;
        padding-top: 37px;
        padding-bottom: 30px; }
    &__logo {
        display: block;
        width: 130px;
        height: 39px;
        background-image: url('../images/footer/footer-logo.svg');
        background-size: contain;
        transition: 0.5s all;
        &:hover {
            background-image: url('../images/footer/hover/footer-logo-hover.svg'); } }
    &__copy {
        min-width: 240px;
        width: 240px;
        padding-right: 20px; }
    &__text:nth-child(2) {
        margin-top: 17px; }
    &__text:nth-child(3) {
        margin-top: 10px; }
    &__text:nth-child(4) {
        margin-top: 20px;
        margin-left: -7px; }
    &__link {
        margin-left: -7px;
        color: $color-footer-text;
        &:hover {
            text-decoration: underline; } } }

.footer {
    &-support {
        &__text {
            display: block; }
        &__link {
            color: $color-footer-text; }
        &:hover {
            text-decoration: underline; } }
    &__support {
        margin-top: 20px;
        margin-left: -7px; } }

.footer {
    &-menus {
        position: relative;
        display: flex;
        justify-content: space-between;
        flex-basis: 100%;
        &__soc {
            position: absolute;
            width: 100%;
            max-width: 370px;
            bottom: 12px;
            @include flex-sb(); }
        &__link {
            width: 45px;
            height: 45px;
            background-repeat: no-repeat;
            transition: .3s ease-out; }
        &__link:nth-child(1) {
            background-image: url('../images/footer/ok-icon.svg'); }
        &__link:nth-child(1):hover {
            background-image: url('../images/footer/hover/ok-icon_hover.svg'); }
        &__link:nth-child(2) {
            background-image: url('../images/footer/fb-icon.svg'); }
        &__link:nth-child(2):hover {
            background-image: url('../images/footer/hover/fb-icon-hover.svg'); }
        &__link:nth-child(3) {
            background-image: url('../images/footer/tw-icon.svg'); }
        &__link:nth-child(3):hover {
            background-image: url('../images/footer/hover/tw-icon-hover.svg'); }
        &__link:nth-child(4) {
            background-image: url('../images/footer/vk-icon.svg'); }
        &__link:nth-child(4):hover {
            background-image: url('../images/footer/hover/vk-icon-hover.svg'); }
        &__link:nth-child(5) {
            background-image: url('../images/footer/insta-icon.svg'); }
        &__link:nth-child(5):hover {
            background-image: url('../images/footer/hover/insta-icon-hover.svg'); } } }


.footer {
    &-menu {
        width: 33.333333%;
        &__title {
            font-size: 16px;
            font-weight: 700;
            text-transform: uppercase; }
        &__list {
            margin-top: 20px; }
        &__link {
            display: block;
            color: $color-footer-text;
            &:hover {
                color: $cokor-btn; } }
        &__contact {
            margin-top: 50px; } } }

.footer {
    &-contact {
        &__title {
            text-transform: uppercase; }
        &__item {
            margin-bottom: 25px; }
        &__link {
            font-weight: 700;
            color: $color-select-phone-link; } } }

.footer {
    &-search {
        display: flex;
        justify-content: flex-start;
        &__input {
            width: 157px;
            height: 45px;
            font-size: 16px;
            line-height: 45px;
            border: none;
            outline: none;
            border-radius: 5px 0 0 5px;
            margin-bottom: 11px;
            padding-left: 19px; }
        &__btn {
            cursor: pointer;
            width: 53px;
            height: 45px;
            border: none;
            outline: none;
            background-color: $color-white;
            background-image: url('../images/footer/footer-search-icon.svg');
            background-repeat: no-repeat;
            background-position: 50% 50%;
            border-left: 2px solid #b9b9b9;
            border-radius: 0 5px 5px 0; } } }
