*,
*::before,
*::after {
    box-sizing: inherit; }

html {
    box-sizing: border-box; }

body {
    margin: 0;
    background-color: $color-white;
    font-size: 14px;
    line-height: 22px;
    font-family: 'Exo 2', 'Open Sans', 'Arial', sans-serif;
    font-weight: 400; }

img {
    max-width: 100%; }

a {
    margin: 0;
    padding: 0;
    text-decoration: none; }

ul {
    padding: 0;
    list-style: none; }

.is-outer {
    position: relative !important;
    left: -9999px !important; }

.site-container {
    overflow: hidden; }

.stop-scroll {
    @include stop-scroll; }

.is-hidden {
    display: none !important;
    @include mobile {
        display: block; } }

.error {
    border: 2px solid #ff4019 !important; }

.visually-hidden {
    @include visually-hidden; }

.btn--reset {
    padding: 0;
    border: none;
    background: transparent;
    cursor: pointer; }

.content {
    max-width: 960px;
    padding: 0 15px;
    margin: 0 auto; }

.page {
    overflow: hidden;
    display: flex;
    flex-direction: column;
    min-height: 100vh; }
