@import './mixins/breakpoints';
@import './mixins/_burger';
@import './mixins/_stop-scroll';
@import './mixins/_visually-hidden';

@mixin flex-sb() {
    display: flex;
    justify-content: space-between;
    align-items: center; }

@mixin flex-start() {
    display: flex;
    justify-content: flex-start;
    align-items: center; }

@mixin flex-c() {
    display: flex;
    justify-content: center;
    align-items: center; }

@mixin flex-end() {
    display: flex;
    justify-content: flex-end;
    align-items: center; }
